body {
  background: #141113;
  color: #F8FFF4;
  font-family: "Inter", sans-serif, serif;
}

button {
  background: #386150;
}

.container {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    max-width: 700px;
  }
}

header {
  font-family: "Quicksand";
  margin-top: 90px;
  margin: 90px 0 17px 0;
}
header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .logo {
  font-weight: 800;
  font-size: 1.5rem;
}
header a {
  color: #F8FFF4;
  text-decoration: none;
}
header ul {
  padding: 0;
  margin: 0;
  font-weight: 600;
}
header ul li {
  display: inline-block;
  font-size: 1.25rem;
  margin-right: 28px;
}
header ul li:last-child {
  margin-right: 0;
}

.intro {
  padding: 40px 45px;
  border: 2px solid #485245;
  border-radius: 11px;
  display: flex;
  grid-gap: 1.5em;
}
.intro .image img {
  border-radius: 50%;
  width: 100%;
  max-width: 194px;
}
.intro__button-group {
  position: absolute;
  left: 0;
  bottom: 15px;
}
.intro .content {
  position: relative;
}
.intro .content h1 {
  font-size: 1.5rem;
  margin: 0;
}
.intro .content p {
  width: 100%;
  max-width: 366px;
  font-size: 1.125rem;
  line-height: 1.3em;
  margin: 0 0 15px 0;
}
.intro .content a.button {
  background: #332323;
  box-shadow: 4px 4px 0px #000000;
  padding: 9px 11px;
  text-decoration: none;
  color: #fff;
  margin-right: 8px;
}
.intro .content a.button:last-child {
  margin-right: 0;
}